import { WebConnectors } from '../..';
import { WebConnectorMessageTypes } from '../../enum';
import { AjaxRequestPayload } from '../../types/ajaxRequestPayload';
import { AjaxRequestListener } from '../../types/web-request-listeners';

export async function addAjaxRequestListener(
  this: WebConnectors,
  { urlPattern, handler }: AjaxRequestListener,
) {
  const listenerId = await this.messaging.sendMessageWithHandler<AjaxRequestPayload>({
    type: WebConnectorMessageTypes.addAjaxRequestListener,
    payload: { urlPattern },
    handler,
  });

  return listenerId
    ? {
        remove: () => this.removeAjaxRequestListener({ listenerId: listenerId as string }),
        listenerId,
      }
    : false;
}
