import React, { useCallback, useState, useEffect, useMemo } from 'react';
import './index.less';
import classNames from 'classnames';

export interface CodePreviewProps {
  componentName?: string;
  args?: any;
  smallFont?: boolean;
  reactFormat?: boolean;
  jsFormat?: boolean;
  cssFormat?: boolean;
}

const CodePreview = ({
  componentName,
  args,
  reactFormat = true,
  jsFormat = false,
  cssFormat = false,
  smallFont = false,
}: CodePreviewProps) => {
  const [isCopied, setIsCopied] = useState(false);
  const [activeTab, setActiveTab] = useState('');
  const [isCollapsed, setIsCollapsed] = useState(true);

  useEffect(() => {
    if (reactFormat) setActiveTab('React');
    else if (jsFormat) setActiveTab('JS');
    else if (cssFormat) setActiveTab('CSS');
  }, [reactFormat, jsFormat, cssFormat]);

  const generateCode = useCallback(() => {
    if (activeTab === 'React') {
      return `<${componentName} ${Object.entries(args)
        .filter(([_, value]) => value !== undefined)
        .map(([key, value]) =>
          typeof value === 'boolean'
            ? value
              ? `${key}`
              : `${key}={false}`
            : `${key}=${JSON.stringify(value)}`,
        )
        .join(' ')} />`;
    }

    if (activeTab === 'JS') {
      return `{
${Object.entries(args)
  .map(([key, value]) => {
    const formattedKey = key.replace(/"|'/g, '');
    const formattedValue = typeof value === 'string' ? `'${value}'` : value;
    return `  ${formattedKey}: ${formattedValue},`;
  })
  .join('\n')}
}`;
    }

    if (activeTab === 'CSS') {
      return Object.entries(args)
        .filter(([_, value]) => value !== undefined)
        .map(([key, value]) => {
          const cssVarName = `--${key.replace(/[A-Z]/g, (match) => `-${match.toLowerCase()}`)}`;
          return `${cssVarName}: ${value};`;
        })
        .join('\n');
    }

    return '';
  }, [activeTab, componentName, args]);

  const handleCopyReset = useCallback(() => {
    setTimeout(() => setIsCopied(false), 1000);
  }, []);

  const copyCode = useCallback(async () => {
    const code = generateCode();
    try {
      await navigator.clipboard.writeText(code);
      setIsCopied(true);
      handleCopyReset();
    } catch (error) {
      console.error('Failed to copy code:', error);
    }
  }, [generateCode, handleCopyReset]);

  const handleClick = useCallback(() => {
    void copyCode();
  }, [copyCode]);

  const handleTabClick = useCallback(
    (tab: string) => {
      if (isCollapsed) {
        setIsCollapsed(false);
      }
      setActiveTab(tab);
    },
    [isCollapsed],
  );

  const tabHandlers = useMemo(
    () => ({
      handleReactTabClick: () => handleTabClick('React'),
      handleJsTabClick: () => handleTabClick('JS'),
      handleCssTabClick: () => handleTabClick('CSS'),
    }),
    [handleTabClick],
  );

  const handleCollapse = useCallback(() => {
    setIsCollapsed((prevState) => !prevState);
  }, []);

  return (
    <div
      className={classNames(
        'code-preview-box',
        smallFont ? 'small-font-size' : '',
        isCollapsed ? 'collapsed' : 'expanded',
      )}
    >
      <div className="tabs">
        {reactFormat && (
          <button
            type="button"
            onClick={tabHandlers.handleReactTabClick}
            className={activeTab === 'React' ? 'active' : ''}
          >
            React
          </button>
        )}
        {jsFormat && (
          <button
            type="button"
            onClick={tabHandlers.handleJsTabClick}
            className={activeTab === 'JS' ? 'active' : ''}
          >
            JS
          </button>
        )}
        {cssFormat && (
          <button
            type="button"
            onClick={tabHandlers.handleCssTabClick}
            className={activeTab === 'CSS' ? 'active' : ''}
          >
            CSS
          </button>
        )}
        <button type="button" className="toggle-collapse-btn" onClick={handleCollapse}>
          {isCollapsed ? 'Show code preview' : 'Hide code preview'}
          &nbsp;
          <i className="icon-chevron-up i-va-fix-2" />
        </button>
      </div>

      <div className="code-content">
        {activeTab === 'React' && reactFormat && (
          <>
            <div className="import">
              import <span className="blue">{componentName}</span> from{' '}
              <span className="green">'@getvim/atomic-ui'</span>
            </div>
            <br />
            <div>{generateCode()}</div>
          </>
        )}
        {activeTab === 'JS' && jsFormat && <div>{generateCode()}</div>}
        {activeTab === 'CSS' && cssFormat && <div>{generateCode()}</div>}
      </div>

      <button
        type="button"
        onClick={handleClick}
        className={`copy-btn ${isCopied ? 'copied' : ''}`}
      >
        {isCopied ? (
          <>
            <i className="icon-copy i-va-fix-2" />
            &nbsp;Copied
            <i className="icon-check" />
          </>
        ) : (
          <>
            <i className="icon-copy i-va-fix-2" /> Copy
          </>
        )}
      </button>
    </div>
  );
};

export default CodePreview;
