import { isEmpty, map, keyBy, mapValues, cloneDeep, reduce } from 'lodash-es';
import { BareboneStorage } from '@getvim-os/barebone/storage';
import { Logger, LoggerFactory } from '@getvim-os/logger';
import { ApplicationManifest, ApplicationType, AppStoreServiceManifest } from '@getvim-os/types';
import { inject, injectable } from 'inversify';
import { OsState } from '..';
import { BaseStateManager } from '../lib/baseStateManager.service';
import { InternalApi } from '@/internal-api';
import { Dictionary } from '@reduxjs/toolkit';

export const AUTO_POPUP_DELAY = 1000;

export const APP_OVERRIDES_KEY = 'appOverrides';

type overrides = {
  [ApplicationType.SharedService]: Record<string, AppStoreServiceManifest>;
  [ApplicationType.applicationUI]: Record<string, ApplicationOverrides>;
};

type ApplicationOverrides = {
  manifest?: ApplicationManifest;
  url?: string;
  tokenEndpoint?: string;
  launchEndpoint?: string;
};

@injectable()
export class ApplicationOverridesManager extends BaseStateManager {
  private logger: Logger;
  constructor(
    @inject(OsState) osState: OsState,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
    @inject(BareboneStorage) private storage: BareboneStorage,
    @inject(InternalApi) private internalApi: InternalApi,
  ) {
    super(osState);
    this.logger = loggerFactory.create('ApplicationOverridesManager');
  }

  public async getApplicationOverrides(
    orgManifests: ApplicationManifest[],
  ): Promise<{ [id: string]: ApplicationManifest | undefined }> {
    try {
      const orgManifestsMap: Dictionary<ApplicationManifest> = keyBy(orgManifests, 'metadata.id');

      const appOverrides: overrides = await this.getOverrides();

      if (isEmpty(appOverrides)) {
        return {};
      }

      return mapValues(orgManifestsMap, (originApp, appId) => {
        const appOverridesValues: ApplicationOverrides | undefined =
          appOverrides[ApplicationType.applicationUI][appId];

        let finalApp: ApplicationManifest = cloneDeep<ApplicationManifest>(
          originApp as ApplicationManifest,
        );
        if (appOverridesValues) {
          finalApp = this.buildAppOverrides({
            finalApp,
            appOverridesValues,
            appId,
          });
        }
        let serviceWasOverridden = false;
        const originAppServices = finalApp?.metadata?.services || [];
        if (originAppServices.length > 0) {
          originAppServices.forEach((service, index) => {
            const serviceOverride = appOverrides[ApplicationType.SharedService][service.id];
            if (serviceOverride) {
              serviceWasOverridden = true;
              originAppServices[index] = serviceOverride;
            }
          });
        }
        return serviceWasOverridden || appOverridesValues ? finalApp : undefined;
      });
    } catch (error) {
      this.logger.error('error fetching app overrides!', { error: error });
      return {};
    }
  }

  private buildAppOverrides({
    finalApp,
    appOverridesValues,
    appId,
  }: {
    finalApp: ApplicationManifest;
    appOverridesValues: ApplicationOverrides;
    appId: string;
  }) {
    const { url, tokenEndpoint, launchEndpoint, manifest } = appOverridesValues;
    if (manifest) {
      finalApp = cloneDeep(manifest);
      this.logger.debug('Setting manifest override for app.', { appId });
    }

    if (finalApp && url) {
      const replaceBaseUrl = (originalUrl: string) => {
        const originalUrlObj = new URL(originalUrl);
        const newLaunch = new URL(url);
        newLaunch.pathname = originalUrlObj.pathname;
        newLaunch.search = originalUrlObj.search;
        return newLaunch.toString();
      };

      finalApp = {
        ...finalApp,
        metadata: {
          ...finalApp.metadata,
          url,
          launchEndpoint: finalApp.metadata.launchEndpoint
            ? replaceBaseUrl(finalApp.metadata.launchEndpoint)
            : undefined,
          tokenEndpoint: finalApp.metadata.tokenEndpoint
            ? replaceBaseUrl(finalApp.metadata.tokenEndpoint)
            : undefined,
        },
      };
      this.logger.debug('Setting url override for app.', { appId, url });
    }

    if (tokenEndpoint && finalApp) {
      finalApp.metadata.tokenEndpoint = tokenEndpoint;
    }

    if (launchEndpoint && finalApp) {
      finalApp.metadata.launchEndpoint = launchEndpoint;
    }
    return finalApp;
  }

  private async getOverrides(): Promise<overrides> {
    const appOverrides = await this.storage.safeGet<{
      url: string;
      version: string;
      tokenEndpoint: string;
      launchEndpoint: string;
    }>(APP_OVERRIDES_KEY, this.logger);

    if (isEmpty(appOverrides)) {
      return {
        [ApplicationType.SharedService]: {} as Record<string, AppStoreServiceManifest>,
        [ApplicationType.applicationUI]: {} as Record<string, ApplicationOverrides>,
      };
    }

    this.logger.debug('Fetched application overrides from storage.', { appOverrides });

    const applicationVersionOverrides = map(appOverrides, (value, key) => {
      return { appId: key, version: value.version };
    }).filter((override) => !!override.version);

    const applicationVersionsResponse =
      applicationVersionOverrides.length > 0
        ? await this.internalApi.applicationVersionAPI.getApplicationVersions(
            {
              applicationVersions: applicationVersionOverrides,
            },
            'ApplicationVersionOverrideQuery',
          )
        : undefined;

    const appToManifest = applicationVersionsResponse?.applicationVersions
      ? keyBy(applicationVersionsResponse?.applicationVersions, 'appId')
      : {};

    return reduce(
      appOverrides,
      (acc, value, key) => {
        if (appToManifest[key]?.applicationType === ApplicationType.SharedService) {
          acc[ApplicationType.SharedService][key] = appToManifest[key]
            ?.versionManifest as AppStoreServiceManifest;
          return acc;
        }
        const appOverride = {
          manifest: (appToManifest[key]?.versionManifest as ApplicationManifest)
            ? { ...appToManifest[key].versionManifest, version: appToManifest[key].version }
            : undefined,
          url: value.url,
          tokenEndpoint: value.tokenEndpoint,
          launchEndpoint: value.launchEndpoint,
        };
        acc[ApplicationType.applicationUI][key] = appOverride as ApplicationOverrides;
        return acc;
      },
      {
        [ApplicationType.SharedService]: {} as Record<string, AppStoreServiceManifest>,
        [ApplicationType.applicationUI]: {} as Record<string, ApplicationOverrides>,
      },
    );
  }
}
